___CSS_LOADER_EXPORT___.push([module.id, `:root {
    font-family: Avenir, Helvetica Neue, Segoe UI, Helvetica, arial, sans-serif;
}

a {
    color: black;
}

.pico-content {
    line-height: 1.2;
}

#background-map {
    background-color: white;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

blockquote {
    margin: 1em 0;
    font-style: italic;
}

blockquote cite {
    font-style: normal;
    vertical-align: super;
    font-size: smaller;
}

blockquote cite a:not(:hover) {
    text-decoration: none;
}

form {
    width: 500px;
}

.form-row {
    display: block;
    width: 500px;
    margin-top: 10px;
}

.form-row > label {
    min-width: 150px;
    display: inline-block;
}

.form-row > input, select, button {
    width: 200px;
    display: inline-block;
    box-sizing: border-box;
}

.failures {
    color: red;
}

ul.failures {
    max-height: 25vh;
    overflow-y: auto;
    border: 1px solid gray;
    padding-top: 1em;
    padding-bottom: 1em;
}
